import React from "react"
import styled from "@emotion/styled"

function Pagenation({ page, setPage, postNum, perPage }) {
  const changePage = page => {
    setPage(page)
  }

  return (
    <div style={{ display: "flex", justifyContent: "center" }}>
      {Array.from({ length: Math.ceil(postNum / perPage) }).map((_, index) => {
        if (page === index + 1) {
          return (
            <CurrentPage
              key={`page-${index}`}
              onClick={() => changePage(index + 1)}
              disabled
            >
              {index + 1}
            </CurrentPage>
          )
        }

        return (
          <Button key={`page-${index}`} onClick={() => changePage(index + 1)}>
            {index + 1}
          </Button>
        )
      })}
    </div>
  )
}

export default Pagenation

const Button = styled.button`
  &:hover {
    cursor: pointer;
  }

  background: white;

  border: none;

  font-size: 1.5rem;
  padding: 0.5rem;
`

const CurrentPage = styled.button`
  background: white;

  border: none;

  font-size: 1.5rem;

  padding: 0.5rem;
`
